.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #020305;
  padding: 86px 0;
  .container {
    max-width: 1760px;
  }
  &__inner {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 32px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 27px;
  }
  &__logo {
    width: 145px;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__search,
  &__user,
  &__basket,
  &__favorite {
    max-width: 22px;
    img {
      max-width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__search {
    background: transparent;
    padding: 0;
  }
}
.burger {
  position: relative;
  width: 32px;
  height: 18px;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background: #fff;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
}

@media screen and (max-width: 1200px) {
  .header {
    padding: 50px 0;
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 32px 0;
    &__logo {
      width: 100px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    position: static;
    background: #fff;
    padding: 16px 0;
    &__left {
      gap: 8px;
    }
    &__right {
      gap: 20px;
    }
    &__logo {
      width: 67px;
    }
    &__search,
    &__user,
    &__basket,
    &__favorite {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }

  }
  .burger {
    width: 22px;
    height: 13px;
    span {
      background: #575757;
    }
    &--active {
      span:nth-child(1) {
        top: 6px;
      }
      span:nth-child(3) {
        bottom: 6px;
      }
    }
  }
}