.footer {
  background: #000;
  color: #fff;
  padding: 250px 0 40px;
}
.footer-top {
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 130px;
  }
  &__items {
    display: flex;
    gap: 130px;
  }
  &__subtitle {
    font-size: 17px;
    line-height: 120%;
    text-transform: uppercase;
  }
  &__btn {
    width: 100%;
    height: 50px;
    background: transparent;
    border: 1px solid #fff;
    padding: 5px;
    margin-top: 16px;
    &:hover {
      background: #fff;
      span {
        color: #000;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: #fff;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    margin-top: 12px;
    li {
      display: block;
      font-size: 14px;
      line-height: 120%;
      a {
        display: block;
      }
    }
  }
  &__socials {
    display: flex;
    gap: 16px;
    margin-top: 32px;
  }
  &__social {
    max-width: 32px;
    img {
      max-width: 100%;
    }
  }
}
.footer-bottom {
  padding-top: 215px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 32px;
  }
  &__text {
    font-size: 14px;
    line-height: 120%;
    text-transform: uppercase;
    span {
      margin-right: 5px;
    }
  }
  &__rights {
    font-size: 10px;
    line-height: 120%;
    color: #777575;
    margin-left: 13px;
    margin-top: 3px;
  }
  &__logo {
    width: 105px;
    display: block;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    padding-top: 100px;
  }
  .footer-top {
    &__inner {
      gap: 60px;
    }
    &__items {
      gap: 60px;
    }
  }
  .footer-bottom {
    padding-top: 100px;
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 40px 0 80px;
  }
  .footer-top {
    &__inner {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 40px;
    }
    &__items {
      gap: 40px;
    }
  }
  .footer-bottom {
    padding-top: 40px;
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__subtitle {
      font-size: 10px;
    }
    &__btn {
      min-width: 240px;
      height: 20px;
      margin-top: 24px;
      span {
        font-size: 10px;
      }
    }
    &__items {
      flex-direction: column;
      align-items: center;
    }
    &__list {
      margin-top: 16px;
      li {
        font-size: 10px;
      }
    }
    &__socials {
      gap: 8px;
      margin-top: 24px;
    }
    &__social {
      max-width: 14px;
    }
  }
  .footer-bottom {
    &__inner {
      position: relative;
      flex-direction: column;
      gap: 24px;
      padding-right: 30px;
    }
    &__text {
      font-size: 10px;
    }
    &__rights {
      font-size: 10px;
      margin-left: 9px;
    }
    .btn-up {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}