.menu {
  position: fixed;
  z-index: 15;
  top: 0;
  left: -105%;
  max-width: 500px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #fff;
  padding: 80px 70px 30px;
  transition: all 0.5s;
  overflow-y: auto;
  &--active {
    left: 0;
  }
  &__top {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__close {
    width: 27px;
    cursor: pointer;
    svg {
      width: 100%;
    }
  }
  &__logo {
    display: block;
    width: 240px;
    img {
      width: 100%;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 60px;
  }
  &__item {
    font-size: 14px;
    line-height: 120%;
    transition: all 0.3s;
    &:hover {
      color: #575757;
    }
    a {
      display: block;
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 17px;
    margin-top: 70px;
  }
  &__btn {
    width: 100%;
    height: 50px;
    background: #000;
    padding: 5px;
    &:hover {
      background: #B3B3B3;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: #fff;
      text-transform: uppercase;
    }
  }
  &__btn2 {
    width: 100%;
    height: 50px;
    background: #B3B3B3;
    padding: 5px;
    &:hover {
      background: #000;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: #fff;
      text-transform: uppercase;
    }
  }
  &__bottom {
    margin-top: 70px;
  }
  &__text {
    font-size: 14px;
    line-height: 120%;
    text-transform: uppercase;
  }
  &__rights {
    font-size: 10px;
    line-height: 120%;
    color: #777575;
    margin-top: 2px;
  }
  &__group2 {
    position: relative;
    display: none;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 2px 16px;
    padding-top: 24px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% + 20px);
      height: 1px;
      background: #797878;
    }
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 8px;
    &-icon {
      width: 14px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 10px;
      line-height: 120%;
      text-transform: uppercase;
    }
  }
  &__chat {
    font-size: 10px;
    line-height: 120%;
    text-transform: uppercase;
    background: transparent;
    padding: 0;
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 15px;
  &:after {
    display: none;
  }
  img {
    width: 100%;
  }
  img:nth-child(2) {
    display: none;
  }
}
.btn-up {
  width: 8px;
  cursor: pointer;
  transition: all 0.3s;
  img {
    width: 100%;
  }
  img:nth-child(2) {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .menu {
    padding-top: 43px;
  }
}

@media screen and (max-width: 992px) {
  .menu {
    padding: 23px 60px 30px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 8px;
  }
}

@media screen and (max-width: 576px) {
  .menu {
    max-width: 100%;
    padding: 14px 10px 24px 10px;
    &__top {
      gap: 10px;
    }
    &__close {
      width: 15px;
    }
    &__logo {
      width: 96px;
    }
    &__item {
      font-size: 10px;
    }
    &__subtitle {
      font-size: 10px;
    }
    &__btns {
      gap: 10px;
      margin-top: 60px;
    }
    &__btn,
    &__btn2 {
      height: 40px;
      span {
        font-size: 10px;
      }
    }
    &__bottom {
      margin-top: 60px;
    }
    &__group {
      display: none;
    }
    &__group2 {
      display: flex;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 4px;
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
  .btn-up {
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
}