.main {
  color: #fff;
  background: #020305;
  &__inner {
    position: relative;
  }
  &__slider {
    position: relative;
  }
  .swiper-slide {
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .swiper-button-prev {
    left: max( 30px, calc((100% - 1700px) / 2));
  }
  .swiper-button-next {
    right: max( 30px, calc((100% - 1700px) / 2));
  }
  &__group {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
  }
  &__text {
    font-size: 10px;
    line-height: 120%;
  }
  &__link {
    margin-top: 22px;
    span {
      font-size: 14px;
      line-height: 120%;
      text-decoration: underline;
      text-underline-offset: 2px;
    }
  }
  &__link2 {
    width: 100%;
    background: #000;
    padding: 10px 0;
    margin-top: 15px;
    span {
      font-size: 14px;
      line-height: 120%;
    }
  }
}
.products {
  padding: 140px 0;
  &__slider {
    position: relative;
  }
  .swiper-button-prev {
    left: -70px;
  }
  .swiper-button-next {
    right: -70px;
  }
}
.product-card {
  &__img {
    position: relative;
    display: block;
    padding-bottom: 150%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.25);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 0.5s;
    }
  }
  &__desc {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-top: 16px;
  }
  &__title {
    display: block;
    font-family: 'Inter';
    font-size: 14px;
    line-height: 120%;
    text-transform: uppercase;
  }
  &__price {
    font-family: 'Inter';
    font-size: 15px;
    line-height: 120%;
    margin-top: 4px;
  }
  &__favorite {
    width: 33px;
    flex-shrink: 0;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.fashion {
  color: #fff;
  &__inner {
    position: relative;
  }
  &__top {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #000;
    padding: 10px 0;
    span {
      font-size: 14px;
      line-height: 120%;
      text-transform: uppercase;
    }
  }
  &__slider {
    position: relative;
  }
  .swiper-slide {
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__group {
    position: absolute;
    z-index: 2;
    left: max(15px, calc((100% - 1700px) / 2));
    bottom: 130px;
    text-transform: uppercase;
  }
  &__title {
    font-size: 50px;
    line-height: 120%;
  }
  &__subtitle {
    font-size: 14px;
    line-height: 120%;
    margin-top: 16px;
  }
  .swiper-button-prev {
    left: max( 30px, calc((100% - 1700px) / 2));
  }
  .swiper-button-next {
    right: max( 30px, calc((100% - 1700px) / 2));
  }
}
.products2 {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    margin-right: max(15px, calc((100% - 1700px) / 2));
  }
  &__img {
    max-width: 810px;
    width: calc(100% - 492px);
    img {
      width: 100%;
    }
  }
  &__slider {
    position: relative;
    width: 460px;
    flex-shrink: 0;
    padding: 0 30px;
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
}

@media screen and (max-width: 1600px) {
  .products {
    &__slider {
      padding: 0 40px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1200px) {
  .products {
    padding: 100px 0;
  }
  .fashion {
    &__group {
      bottom: 50px;
    }
  }
  .products2 {
    &__slider {
      width: 400px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    .swiper-button-prev {
      left: max( 15px, calc((100% - 1700px) / 2));
    }
    .swiper-button-next {
      right: max( 15px, calc((100% - 1700px) / 2));
    }
  }
  .products {
    &__slider {
      padding: 0 32px;
    }
  }
  .fashion {
    &__desc {
      left: max( 15px, calc((100% - 1700px) / 2));
    }
    &__title {
      font-size: 32px;
    }
    &__subtitle {
      font-size: 12px;
      margin-top: 8px;
    }
    .swiper-button-prev {
      left: max( 15px, calc((100% - 1700px) / 2));
    }
    .swiper-button-next {
      right: max( 15px, calc((100% - 1700px) / 2));
    }
  }
  .products2 {
    padding-bottom: 100px;
    &__inner {
      flex-direction: column;
      gap: 44px;
      margin: 0;
    }
    &__img {
      width: 100%;
      max-width: 100%;
    }
    &__slider {
      width: 460px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__link {
      margin-top: 5px;
    }
    &__link2 {
      padding: 5px 0;
      margin-top: 10px;
    }
  }
  .products {
    padding: 44px 0;
    &__slider {
      padding: 0 17px;
    }
    .swiper-button-prev {
      left: 8px;
    }
    .swiper-button-next {
      right: 8px;
    }
  }
  .product-card {
    &__desc {
      margin-top: 10px;
    }
    &__title {
      font-size: 10px;
    }
    &__price {
      font-size: 10px;
    }
    &__favorite {
      width: 18px;
    }
  }
  .fashion {
    &__top {
      padding: 5px 0;
      span {
        font-size: 10px;
      }
    }
    &__title {
      font-size: 15px;
    }
    &__subtitle {
      font-size: 8px;
    }
  }
  .products2 {
    &__slider {
      width: 100%;
      padding: 0 17px;
    }
    .swiper-button-prev {
      left: 8px;
    }
    .swiper-button-next {
      right: 8px;
    }
  }
}

@media screen and (max-width: 450px) {
  .main {
    .swiper-slide {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  .fashion {
    .swiper-slide {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__group {
      bottom: 40px;
    }
  }
}